import { useMemo } from 'react';
import { useCurrentChain, useIsEvm, useIsSolana, useParticleKitChains } from '../provider/ModalChainContext';
import { useAccountInfo, useConnectKit } from '../provider/ModalContext';
export function useNetwork() {
    const chains = useParticleKitChains();
    const chain = useCurrentChain();
    const { isEvm, updateIsEvm, changeNetWork } = useIsEvm();
    const { isSolana, updateIsSolana } = useIsSolana();
    const connectKit = useConnectKit();
    const { connectId } = useAccountInfo();

    return useMemo(
        () => ({
            chain,
            chains,
            isEvm,
            isSolana,
            network: isEvm ? 'evm' : isSolana ? 'solana' : undefined,
            connectId,
            changeNetWork,
        }),
        [chain, chains, isEvm, isSolana, updateIsEvm, updateIsSolana, connectKit, connectId, changeNetWork]
    );
}
