import { AuthCoreEvent, AuthType, isConnected, particleAuth } from '@particle-network/auth-core';
import { useConnect, useEthereum, useSolana } from '@particle-network/auth-core-modal';
import type { Provider } from '@particle-network/connectors';
import { getEVMChainById, getPublicAddress, isEVMProvider } from '@particle-network/connectors';
import { walletEntryPlugin } from '@particle-network/wallet';
import { useCallback, useEffect } from 'react';
import {
    useAccountInfo,
    useChain,
    useConnectAction,
    useConnectKit,
    useIsCacheProvider,
    useUnsupportedChain,
    useUserChains,
} from '../provider/ModalContext';
import { useNetwork } from './useNetwork';

interface ConnectOptions {
    id?: string;
    preferredAuthType?: AuthType;
    [key: string]: any;
}

export function useParticleConnect() {
    const { updateParticleProvider, updateAccount, updateConnectId } = useConnectAction();
    const { updateChain } = useChain();
    const { setAccountLoading, connectId, particleProvider } = useAccountInfo();
    const { changeNetWork } = useNetwork();
    const { userChains } = useUserChains();
    const { setUnsupportedChain } = useUnsupportedChain();
    const isCache = useIsCacheProvider();

    const connectKit = useConnectKit();

    const connectSuccess = useCallback(
        async (provider: Provider, id?: string) => {
            const account = await getPublicAddress(provider);
            updateParticleProvider?.(provider);
            updateAccount?.(account);
            setAccountLoading?.(false);
            updateConnectId?.(id ?? '');

            if (isEVMProvider(provider)) {
                const chainId: string = await provider.request({ method: 'eth_chainId' });
                const chain = getEVMChainById(Number(chainId));
                if (chain) {
                    updateChain?.({
                        id: Number(chainId),
                        name: chain.name,
                    });
                }
                changeNetWork('evm');
                setUnsupportedChain?.(!chain || !userChains.find((x) => x.id === chain.id));
            } else {
                updateChain?.({
                    id: 101,
                    name: 'Solana',
                });
                changeNetWork('solana');
                setUnsupportedChain?.(false);
            }
        },
        [updateChain, changeNetWork, getEVMChainById]
    );

    const { connect: connectAuthCore, disconnect: disconnectAuthCore } = useConnect();

    const { provider: evmProvider } = useEthereum();
    const { wallet: solanaWallet } = useSolana();
    const { connectionStatus } = useConnect();

    useEffect(() => {
        const chain = userChains[0];
        const onAuthCoreConnect = () => {
            connectSuccess(chain.name.toLowerCase() === 'solana' ? solanaWallet : evmProvider, 'particle');
        };
        particleAuth.on(AuthCoreEvent.ParticleAuthConnect, onAuthCoreConnect);
        return () => {
            particleAuth.off(AuthCoreEvent.ParticleAuthConnect, onAuthCoreConnect);
        };
    }, [connectSuccess, userChains]);

    useEffect(() => {
        if (connectionStatus === 'loading') {
            setAccountLoading?.(true);
        } else if (connectionStatus === 'connected' || connectionStatus === 'disconnected') {
            setAccountLoading?.(false);
        }
    }, [connectionStatus]);

    useEffect(() => {
        if (connectId !== 'particle') {
            if (particleProvider) {
                if (isEVMProvider(particleProvider)) {
                    walletEntryPlugin.setWalletCore({
                        ethereum: particleProvider,
                    });
                } else {
                    // phantom 不支持切链
                    Object.defineProperty(particleProvider, 'chainId', {
                        value: 101,
                        writable: false,
                    });
                    Object.defineProperty(particleProvider, 'selectedAddress', {
                        value: (particleProvider.publicKey as any)?.toBase58() || null,
                        writable: false,
                    });
                    walletEntryPlugin.setWalletCore({
                        solana: particleProvider as any,
                    });
                }

                // fix: create 时没有初始化完成
                setTimeout(() => {
                    walletEntryPlugin.walletEntryCreate();
                }, 200);
            } else {
                walletEntryPlugin.walletEntryDestroy();
            }
        }
    }, [connectId, particleProvider]);

    const connect = async ({ preferredAuthType, id, ...options }: ConnectOptions) => {
        if (connectKit) {
            if (id === 'particle') {
                const chain = userChains[0];
                if (preferredAuthType === AuthType.phone) {
                    await connectAuthCore({
                        phone: '',
                        chain,
                    });
                } else if (preferredAuthType === AuthType.email) {
                    await connectAuthCore({
                        email: '',
                        chain,
                    });
                } else {
                    await connectAuthCore({
                        socialType: preferredAuthType as any,
                        prompt: 'select_account',
                        chain,
                    });
                }
                await connectSuccess(chain.name.toLowerCase() === 'solana' ? solanaWallet : evmProvider, 'particle');
                return evmProvider;
            }
            const provider = await connectKit.connect(id!, options);
            await connectSuccess(provider, id);
            return provider;
        } else {
            return Promise.reject('error : connect no found  connectKit');
        }
    };

    const disconnect = async (options?: any) => {
        try {
            if (connectKit) {
                if (connectId === 'particle') {
                    await disconnectAuthCore();
                } else {
                    await connectKit.disconnect(options);
                }
                updateParticleProvider?.(undefined);
                updateAccount?.('');
                updateConnectId?.('');
                setUnsupportedChain?.(false);
                if (typeof window !== 'undefined') {
                    window?.localStorage?.removeItem('WALLETCONNECT_DEEPLINK_CHOICE');
                }
                changeNetWork?.('');
                updateChain?.(undefined);
            }
            return Promise.resolve('no connect');
        } catch (error) {
            return Promise.reject(error);
        }
    };

    const cacheconnect = async (): Promise<Provider | undefined> => {
        if (connectKit && isCache) {
            if (isConnected()) {
                const chain = userChains[0];
                const chainType = localStorage.getItem('particle_connect_chain_type');
                let provider = chain.name.toLowerCase() === 'solana' ? solanaWallet : evmProvider;
                if (chainType === 'solana' && userChains.some((chain) => chain.name.toLowerCase() === 'solana')) {
                    provider = solanaWallet;
                }
                await connectSuccess(provider, 'particle');
                return provider;
            } else {
                const provider = await connectKit.connectToCachedProvider().catch((e) => {
                    console.log('Error:connectToCachedProvider ', e);
                    setAccountLoading?.(false);
                    return undefined;
                });
                if (provider) {
                    await connectSuccess(provider, connectKit?.cachedProviderId?.() || '');
                } else {
                    setAccountLoading?.(false);
                }
                return provider;
            }
        }
        setAccountLoading?.(false);
        return undefined;
    };

    return {
        connectKit,
        connect,
        disconnect,
        cacheconnect,
        _connectSuccess: connectSuccess,
    };
}
