import { useEthereum, useSolana } from '@particle-network/auth-core-modal';
import { chains as chainUtils } from '@particle-network/chains';
import type { Chain, ChainInfo, ParticleConnect, Provider } from '@particle-network/connectors';
import { ParticleChains, isEVMProvider, isSolanaProvider } from '@particle-network/connectors';
import type { ReactNode } from 'react';
import React, { createContext, useCallback, useContext, useMemo, useState } from 'react';

// This type is a combination of wagmi and ParticleKit chain types to make
// it easier for consumers to define their chain config in a single place.
export type ParticleKitChain = Chain;
interface ParticleKitChainContextValue {
    chains: Chain[];
    chain?: Chain;
    isEvm: boolean;
    isSolana: boolean;
    renderChains: any[];
    updateIsEvm?: (bool: boolean) => void;
    updateIsSolana?: (bool: boolean) => void;
    isSwitchChain: boolean;
    switchChain: (chain: Chain) => Promise<void>;
    changeNetWork: (type: string) => void;
}

const ParticleKitChainContext = createContext<ParticleKitChainContextValue>({
    chains: [],
    isEvm: false,
    isSolana: false,
    renderChains: [],
    isSwitchChain: true,
    switchChain: async (chain: Chain) => {},
    changeNetWork: (type: string) => {},
});

interface ParticleKitChainProviderProps {
    chains: Chain[];
    chain?: Chain;
    setChain: (chain: Chain) => void;
    initialChain?: Chain | undefined;
    children: ReactNode;
    userChains: Chain[];
    provider: Provider | undefined;
    connectKit?: ParticleConnect;
    connectId?: string;
}

export function ParticleKitChainProvider({
    chains,
    children,
    chain,
    setChain,
    userChains,
    provider,
    connectKit,
    connectId,
}: ParticleKitChainProviderProps) {
    const [isEvm, setIsEvm] = useState(false);
    const [isSolana, setIsSolana] = useState(false);
    const [unsupportedChain, setUnsupportedChain] = useState(false);
    const updateIsEvm = useCallback((bool: boolean): void => setIsEvm(bool), []);
    const updateIsSolana = useCallback((bool: boolean): void => setIsSolana(bool), []);
    const { switchChain: evmSwitchChain } = useEthereum();
    const { switchChain: solanaSwitchChain } = useSolana();

    const renderChains = useMemo(() => {
        if (connectId && userChains && provider) {
            let rchains = [...userChains];
            const isEvm = provider && isEVMProvider(provider);
            const isSolana = provider && isSolanaProvider(provider);
            // 判断第三方钱包的链是否为evm 去处非solana的链
            if (isSolana && connectId !== 'particle') {
                rchains = rchains.filter((item) => [101, 102, 103].includes(item.id));
            }
            // 判断第三方钱包的链是否为evm 去处solana的链
            if (isEvm && connectId !== 'particle') {
                rchains = rchains.filter((item) => {
                    return ![101, 102, 103].includes(item.id) && item.name.toLowerCase() !== 'tron';
                });
            }

            const rest = rchains.map((chain) => {
                const chainInfo = chainUtils.getChainInfo(chain);
                if (chainInfo) {
                    return chainInfo;
                }
                return {
                    icon: '',
                    network: '',
                    ...chain,
                };
            });
            console.log('renderChains', rest);
            return rest;
        }
        return [];
    }, [connectId, userChains, provider]);

    const isSwitchChain = useMemo(() => {
        if (!connectKit) return false;
        if (!connectId) return false;
        if (connectId === 'particle') return true;
        const connector = connectKit?.getConnector(connectId)?.connector;
        // @ts-ignore
        if (connector && connectId && !!connector?.switchChain) {
            return true;
        } else {
            return false;
        }
    }, [connectId, connectKit]);

    const changeNetWork = useCallback(
        (chainType: string) => {
            console.log('chainType:', chainType);
            localStorage.setItem('particle_connect_chain_type', chainType);
            switch (chainType) {
                case 'solana':
                    updateIsEvm?.(false);
                    updateIsSolana?.(true);
                    break;
                case 'evm':
                    updateIsEvm?.(true);
                    updateIsSolana?.(false);
                    break;
            }
        },
        [updateIsEvm, updateIsSolana]
    );

    const switchChain = useCallback(
        async (targetChain: Chain) => {
            console.log('targetChain targetChain:', targetChain);
            if (!isSwitchChain) {
                throw new Error('Switch chain is not supported');
            }
            if (isSolana && connectId !== 'particle') return;
            if (!chain) {
                return;
            }
            if (chain.id === targetChain.id) throw new Error('The chain is same as the current chain');
            if (connectId === 'particle') {
                if (targetChain?.name.toLowerCase() === 'solana') {
                    await solanaSwitchChain(targetChain.id);
                    changeNetWork('solana');
                } else {
                    await evmSwitchChain(targetChain.id);
                    changeNetWork('evm');
                }
                setChain(targetChain);
            } else {
                await connectKit?.switchChain({ id: targetChain.id, name: targetChain.name });
            }
        },
        [connectKit, connectId, isSolana, chain, changeNetWork, isSwitchChain, setChain]
    );

    return (
        <ParticleKitChainContext.Provider
            value={useMemo(
                () => ({
                    chains,
                    chain,
                    isEvm,
                    isSolana,
                    updateIsSolana,
                    updateIsEvm,
                    unsupportedChain,
                    setUnsupportedChain,
                    renderChains,
                    isSwitchChain,
                    switchChain,
                    changeNetWork,
                }),
                [
                    chains,
                    chain,
                    isEvm,
                    isSolana,
                    updateIsSolana,
                    updateIsEvm,
                    unsupportedChain,
                    setUnsupportedChain,
                    renderChains,
                    isSwitchChain,
                    switchChain,
                    changeNetWork,
                ]
            )}
        >
            {children}
        </ParticleKitChainContext.Provider>
    );
}

export const useParticleKitChains = () => useContext(ParticleKitChainContext).chains;
export const useInitialChainId = () => useContext(ParticleKitChainContext)?.chain?.id;

export const useIsEvm = () => {
    const { updateIsEvm, isEvm, changeNetWork } = useContext(ParticleKitChainContext);
    return { updateIsEvm, isEvm, changeNetWork };
};

export const useIsSolana = () => {
    const { updateIsSolana, isSolana, changeNetWork } = useContext(ParticleKitChainContext);
    return { updateIsSolana, isSolana, changeNetWork };
};

export const useSwitchChains = () => {
    const { isSwitchChain, renderChains, switchChain } = useContext(ParticleKitChainContext);
    return { isSwitchChain, renderChains, switchChain };
};

export const useCurrentChain = (): ChainInfo | undefined => {
    const initialChain = useContext(ParticleKitChainContext)?.chain;
    if (!initialChain) return undefined;
    const findKey = Object.keys(ParticleChains).find((key) => ParticleChains[key].id === initialChain.id);
    if (findKey) return ParticleChains[findKey];
    return undefined;
};
